.app {
  display: flex;
  font-family: 'Inter', sans-serif;
  background-color: #f8f9fb;
  border: 1px solid red;
  width: 100%;
}


.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  
}

.content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 24px 40px;
  box-sizing: border-box;
  border: 1px solid rgb(0, 255, 47);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}



.App-link {
  color: #61dafb;
}


.sidebar {
  width: 219px;
  display: flex;
  height: 823px;
  padding: var(--Spacing-medium, 16px);
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  flex-shrink: 0;
  border-right: 1px solid var(--Color-Surface-surface-100, #EEF1F6);
  background: var(--Color-Surface-surface-0, #FFF);
  box-sizing: border-box;

}



.tessell-logo {
  width: 20.457px;
  height: 18.945px;
  flex-shrink: 0;
}

